import React, { Component } from 'react';
import './Landing.css';
import Fab from '@material-ui/core/Fab';
import Icon from '@material-ui/core/Icon';
import Avatar from '@material-ui/core/Avatar';
import ReactGA from 'react-ga';


class Landing extends Component {
  constructor() {
    super();
    this.state = {
      height: '',
      scrollTo: '',
      fabShow: true
    };
  }

  changeViewPage(uri) {
    this.props.history.push(uri);
  }



  updateDimensions() {
    this.setState({
      height: window.innerHeight + 'px',
      scrollTo: window.innerHeight
    });
  }

  touch() {
    this.setState({ fabShow: !this.state.fabShow });
  }

  async componentDidMount() {
    window.addEventListener("resize", this.updateDimensions.bind(this));
    this.updateDimensions();

  

  }

  async componentWillUnmount() {
    window.removeEventListener("resize", this.updateDimensions.bind(this));
  }



  render() {

    ReactGA.event({
      category: 'public',
      action: 'contact'
    });

    return (
      <div className="section header-section">
        <div className="section-container header-component">


          <div className="header-wrapper" style={{ "height": this.state.height }}>
            <div className="header">
              <Avatar className="bigAvatar" alt="Dhinesh P" src="/img/Dhinesh.jpg" />
              <h3>DHINESH P</h3>
              <h6>FULL STACK DEV</h6>
            </div>


            {this.state.fabShow &&

              <div className=" scroll-down-btn ">
                <Fab color="primary" aria-label="Add" onClick={this.changeViewPage.bind(this, '/skill')}>
                  <Icon>keyboard_arrow_down</Icon>
                </Fab>
              </div>
            }


          </div>

        </div>
      </div>
    );
  }
}

export default Landing;