import { database } from "../config/Config";

export function getSkills() {

    return (dispatch) => database.ref('skills').once('value')
        .then((snapshot) => {
            return snapshot.val();
        })
        .then((skills) => {
          return dispatch(loadSkill(skills));
        });
}

export function loadSkill(skills) {
    return {
        type: 'LOAD_SKILLS',
        skills
    }
}