import React, { Component } from 'react';
import './Skills.css';
import LinearProgress from '@material-ui/core/LinearProgress';
import Fab from '@material-ui/core/Fab';
import Icon from '@material-ui/core/Icon';
import ReactGA from 'react-ga';



class Skills extends Component {

  constructor() {
    super();
    this.state = {
      height: '',
      scrollTo: '',
      fabShow: true
    };
  }


  changeViewPage(uri) {
    this.props.history.push(uri);
  }

  updateDimensions() {
    this.setState({
      height: window.innerHeight + 'px',
      scrollTo: window.innerHeight
    });
  }


  async componentDidMount() {
    window.addEventListener("resize", this.updateDimensions.bind(this));
    this.updateDimensions();


  }

  async componentWillUnmount() {
    window.removeEventListener("resize", this.updateDimensions.bind(this));
  }

  render() {

    ReactGA.event({
      category: 'public',
      action: 'skills'
    });

    return (
      <div className="header-wrapper" style={{ "height": this.state.height }}>

        <div className="header-skill">
          <h3>Skills</h3>
        </div>
        <div className="skill-container">

          {this.props.skills &&
            this.props.skills.map((skill, index) => (
              <div key={index} className="skill-box">
                <div className="skill-name"> {skill.name} </div>
                <LinearProgress className="skill-progress" color="secondary" variant="determinate" value={skill.value} />
              </div>

            ))

          }
        </div>

        <div className=" scroll-down-btn ">
          <Fab color="primary" aria-label="Add" onClick={this.changeViewPage.bind(this, '/contact')}>
            <Icon>keyboard_arrow_down</Icon>
          </Fab>
        </div>

      </div>
    );
  }
}

export default Skills;