import React, { Component } from 'react';
import './Contact.css';
import Fab from '@material-ui/core/Fab';
import Icon from '@material-ui/core/Icon';
import ReactGA from 'react-ga';


class Contact extends Component {

  constructor() {
    super();
    this.state = {
      height: '',
      scrollTo: '',
      fabShow: true
    };
  }


  changeViewPage(uri) {
    this.props.history.push(uri);
  }

  makeAction(contact) {

    if(contact.name === 'github') {
      window.open(contact.url, '_blank');
    }

    if(contact.name === 'linkedin') {
      window.open(contact.url, '_blank');
    }

    if(contact.name === 'email') {
      window.open('mailto:'+contact.email);
    }
    

  }

  updateDimensions() {
    this.setState({
      height: window.innerHeight + 'px',
      scrollTo: window.innerHeight
    });
  }


  async componentDidMount() {
    window.addEventListener("resize", this.updateDimensions.bind(this));
    this.updateDimensions();



  }

  async componentWillUnmount() {
    window.removeEventListener("resize", this.updateDimensions.bind(this));
  }


  contactData = [{
    name: "github",
    url: "https://github.com/Dhinesh18-02",
    icon: "/img/github.svg"
  }, {
    name: "linkedin",
    url: "https://www.linkedin.com/in/dhinesh-p-16a8a0179/",
    icon: "/img/linkedin.svg"
  },
  {
    name: "email",
    url: "gmail.com",
    icon: "/img/email.svg",
    email : "pdhinesh2014@gmail.com"
  }];



  render() {

    ReactGA.event({
      category: 'public',
      action: 'contact'
    });

    return (
      <div className="header-wrapper" style={{ "height": this.state.height }}>

        <div className="header-skill">
          <h3>Contact</h3>
        </div>
        <div className="contact-container">

          <div className="contact-data-container">

            {this.contactData &&
              this.contactData.map((contact, index) => (
                <div key={index} className="contact-cell">

                  <div className="icon-container">
                    <img src={contact.icon} className="icon" onClick={this.makeAction.bind(this,contact)}>
                    </img>
                  </div>

                </div>
              ))
            }
          </div>


        </div>

        <div className=" scroll-down-btn ">
          <Fab color="primary" aria-label="Add" onClick={this.changeViewPage.bind(this, '/')}>
            <Icon>keyboard_arrow_down</Icon>
          </Fab>
        </div>

      </div>
    );
  }
}

export default Contact;